import React from 'react';
import { getClassName } from '@miq/utils';

export const ViewHeader = (props) => {
  const { title, text, actions } = props;
  const header = title || text || actions;

  if (!header) return null;

  return (
    <header className={props.className}>
      <div className="miq-view-header-inner d-flex">
        <div className="miq-view-title">{typeof title === 'string' ? <h2 className="text-md">{title}</h2> : title}</div>

        {actions && <section className="miq-view-actions">{actions}</section>}
      </div>

      {text && <div className="miq-view-text">{text}</div>}
    </header>
  );
};

export default function ViewSection({ children, ...props }) {
  return (
    <div className={getClassName(['miq-view-section', props.className])}>
      <ViewHeader {...props} className="miq-view-header" />
      {children}
    </div>
  );
}
