import React, { useContext, useEffect, useState } from 'react';
import { isFunction } from 'lodash';

import Form from '@miq/form';
import { AdminView } from '@miq/adminjs';
import { Icons, IconButton, ConfirmIconButton, Button, ToastCtx } from '@miq/components';

import DocumentTable from './DocTable';
import { docServices } from './utils';

export const DocumentTitleInput = ({ document = {}, ...props }) => {
  return (
    <Form.TextInput
      autoComplete="off"
      tabIndex={0}
      aria-label="Renommer ce document"
      title="Renommer ce document"
      data-tooltip="Renommer ce document"
      maxLength={99}
      {...props}
      required
      name="title"
      className="document-title text-md"
      disabled={document.is_read_only}
    />
  );
};

/**
 *
 * @param {*} param0
 * @returns
 */

export const DocumentArchiveIconButton = ({ document, ...props }) => {
  if (!document || !document.can_archive) return null;
  const { toast, onSuccess } = props;

  const archiveDocument = () => {
    if (document.status === 'ARCHIVED') {
      if (toast) toast.info({ message: 'Document already archived' });
      return;
    }

    docServices
      .archive(document.slug)
      .then((newDocument) => {
        if (isFunction(onSuccess)) onSuccess(newDocument);
        if (toast) toast.success({ message: 'Document archivé.' });
      })
      .catch(() => {
        if (toast) toast.error({ message: 'Oops! Something went wrong.' });
      });
  };

  return (
    <IconButton
      Icon={Icons.Archive}
      label="Archiver"
      onClick={archiveDocument}
      title="Archiver ce document. Un document archivé ne peut plus être modifié"
      // {...props}
      disabled={!document.can_archive}
      id={props.id}
      className={props.className}
    />
  );
};

export const DocumentApproveIconButton = ({ document, ...props }) => {
  if (!document || !document.can_approve) return null;
  const { toast, onSuccess } = props;

  const approveDocument = () =>
    docServices
      .approve(document.slug)
      .then((newDocument) => {
        if (isFunction(onSuccess)) return onSuccess(newDocument);

        if (toast) toast.success({ message: 'Document apprové.' });
      })
      .catch(() => {
        if (toast) return toast.error({ message: 'Document approval failed.' });
      });

  return (
    <IconButton
      Icon={Icons.Archive}
      label="Approver"
      onClick={approveDocument}
      title="Approver ce document. Un document apprové ne peut plus être modifié"
      disabled={!document.can_approve}
      id={props.id}
      className={props.className}
    />
  );
};

/**
 *
 * @param {*} param0
 * @returns
 */

export const DocumentDeleteIconButton = ({ document, docSlug, onSuccess, ...props }) => {
  const toast = useContext(ToastCtx);
  if (!document || document.is_read_only) return null;

  const deleteDocument = () =>
    docServices
      .delete(docSlug)
      .then(() => {
        if (isFunction(onSuccess)) return onSuccess(docSlug);
        if (toast) toast.success({ message: 'Document deleted.' });
      })
      .catch(() => {
        if (toast) toast.error({ message: 'Document deletion failed.\nPlease try again.' });
      });

  return (
    <ConfirmIconButton
      Icon={Icons.Trash}
      id={props.id}
      label={props.label}
      className={props.className}
      title="Supprimer ce document"
      renderHeader={() => <div className="fw-bold">Supprimer ce document</div>}
      renderFooter={({ setOpen }) => (
        <div className="flex-1 d-flex align-items-center justify-content-between">
          <Button disabled={document.is_read_only} onClick={deleteDocument} className="btn-danger">
            Confirmer
          </Button>
          <Button onClick={() => setOpen(false)}>Annuler</Button>
        </div>
      )}
      render={() => (
        <div className="p-4">
          <p>
            Are you sure you want to delete the document
            <span className="fw-bold">{document.title_truncated}</span>?
          </p>
          <p>This item will be deleted immediately. You can’t undo this action.</p>
        </div>
      )}
      disabled={document.is_read_only}
    />
  );
};

/**
 *
 * @param {*} props
 * @returns
 */

export const DocumentsLatest = (props) => {
  const { title, ...rest } = props;
  const [data, setData] = useState({ results: [] });

  useEffect(() => {
    docServices
      .list()
      .then((data) => {
        setData({ results: data.results.slice(0, 8) });
      })
      .catch((err) => {
        // toasts.error({ message: 'Something went wrong' });
      });
  }, []);

  if (!Boolean(data.results.length)) return null;

  return (
    <AdminView.Section {...props} className="document-latest">
      <DocumentTable {...rest} {...{ data, setData }} path={`${props.match.path}documents/`} />
    </AdminView.Section>
  );
};
