import { Link } from 'react-router-dom';
import PropTypes from 'prop-types';

import { getClassName } from '@miq/utils';
import { Table, ItemTable, AvatarGroup } from '@miq/components';

import { docServices, formatDocDate, getDocumentLinkTo, mapDocumentUser2Avatar } from './utils';
import { DocumentDeleteIconButton } from './components';

const propTypes = {
  data: PropTypes.shape({
    results: PropTypes.array,
    count: PropTypes.number,
    next: PropTypes.string,
    previous: PropTypes.string,
  }).isRequired,
  setData: PropTypes.func.isRequired,
};

export default function DocumentTable(props) {
  const { data = {}, setData } = props;

  const handlePreviousClick = () => {
    if (!data || !data.previous) return;
    return docServices.get(data.previous).then((newData) => {
      setData(newData);
    });
  };

  const handleNextClick = () => {
    if (!data || !data.next) return;
    return docServices.get(data.next).then((newData) => {
      setData(newData);
    });
  };

  return (
    <div className="document-table mb-3">
      <ItemTable
        className="w-100"
        items={data.results}
        renderItem={(item) => <DocumentRow {...props} document={item} key={item.slug} />}
        pagination={{
          count: data.count,
          next: data.next,
          previous: data.previous,
          onPreviousClick: handlePreviousClick,
          onNextClick: handleNextClick,
        }}
      />
    </div>
  );
}
DocumentTable.propTypes = propTypes;

/**
 *  =============> ROW <=============
 */

const DocumentRow = ({ document = {}, ...props }) => {
  const { status } = document;

  const handleDelete = (slug) => {
    props.setData({
      ...props.data,
      count: props.data.count - 1,
      results: props.data.results.filter((i) => i.slug !== slug),
    });
  };

  return (
    <Table.Tr className={getClassName([status === 'ARCHIVED' && 'archived', 'border-bottom'])}>
      <Table.Td className="w-100">
        <Link to={getDocumentLinkTo(document, props.path || props.match.path)}>
          <div className="mb-1" title={document.title}>
            {document.title_truncated}
          </div>

          <AvatarGroup users={mapDocumentUser2Avatar(document)} count={document.users_data.length} />
        </Link>
      </Table.Td>

      <Table.Td className="d-none d-md-table-cell text-sm text-center">
        {document.files.length > 0 && <div>{document.files.length} fichiers</div>}
      </Table.Td>
      <Table.Td className="d-none d-md-table-cell text-sm">{status}</Table.Td>

      <Table.Td className="d-none d-md-table-cell text-sm">{formatDocDate(document.created)}</Table.Td>

      <Table.Td className="">
        <div className="actions d-flex">
          <DocumentDeleteIconButton
            {...props}
            docSlug={document.slug}
            document={document}
            className="border-0"
            onSuccess={handleDelete}
          />
        </div>
      </Table.Td>
    </Table.Tr>
  );
};

DocumentRow.propTypes = {
  ...propTypes,
  document: PropTypes.shape({ slug: PropTypes.string, status: PropTypes.string }),
};
