import { lazy, Suspense } from 'react';
import { Switch } from 'react-router-dom';

import { AdminRoute } from '@miq/adminjs';
import { ToastProvider, useToast, Loading } from '@miq/components';

import './index.scss';

const DocumentSettingsView = lazy(() => import('./SettingsView'));
const DocumentReadOnlyView = lazy(() => import('./document/ReadOnlyView'));
const DocumentShareView = lazy(() => import('./document/ShareView'));
const DocumentUpdateView = lazy(() => import('./document/UpdateView'));
const DocumentIndexView = lazy(() => import('./document/'));

export default function DocumentRoutes(props) {
  const toastCtx = useToast();
  const { path } = props.match;

  return (
    <ToastProvider context={toastCtx}>
      <Suspense fallback={<Loading />}>
        <Switch>
          <AdminRoute
            path={`${path}settings/`}
            component={DocumentSettingsView}
            requiredPerms={
              [
                // 'miq_dms.view_document'
              ]
            }
          />
          <AdminRoute
            path={`${path}:docSlug/view/`}
            component={DocumentReadOnlyView}
            requiredPerms={['miq_dms.view_document']}
          />
          <AdminRoute
            path={`${path}:docSlug/share/`}
            component={DocumentShareView}
            requiredPerms={['miq_dms.change_document']}
          />
          <AdminRoute
            path={`${path}:docSlug/`}
            component={DocumentUpdateView}
            requiredPerms={['miq_dms.change_document']}
          />
          <AdminRoute path={path} component={DocumentIndexView} requiredPerms={['miq_dms.view_document']} />
        </Switch>
      </Suspense>
    </ToastProvider>
  );
}

export { DocumentsLatest } from './document/components';
