import { addForwardSlash, STAFF_PATH } from '@miq/utils/';

class UrlResolver {
  base_path = '';

  constructor(base_path = '', kwargs = {}) {
    Object.assign(this, kwargs);

    if (base_path) {
      this.base_path = addForwardSlash(base_path);
    }
  }

  get staff_path() {
    return `${STAFF_PATH}${this.base_path}`;
  }
}

export { UrlResolver };
