import { forwardRef, createRef } from 'react';
import PropTypes from 'prop-types';

import { services, isRequired, getClassName } from '@miq/utils';

import { FileInput } from '../File';
import { Button } from '../buttons';

const propTypes = {
  slug: PropTypes.string,
  multiple: PropTypes.bool,

  onCreate: PropTypes.func,
  onUpdate: PropTypes.func,
  onError: PropTypes.func,

  children: PropTypes.any,
};

const postImage = (file = isRequired('File'), alt_text = '') => {
  if (file.name.length > 99) {
    return Promise.reject('Filename too long');
  }

  const fd = new FormData();
  fd.append('src', file, file.name);
  fd.append('alt_text', alt_text);

  return services().post(`/api/v1/images/`, fd, {
    headers: { 'content-type': 'multipart/form-data' },
  });
};

const patchImage = (imgSlug, file = isRequired('File'), alt_text = '') => {
  if (file.name.length > 99) {
    return Promise.reject('Filename too long');
  }

  const fd = new FormData();
  fd.append('src', file, file.name);
  fd.append('alt_text', alt_text);

  return services().patch(`/api/v1/images/${imgSlug}/`, fd, {
    headers: { 'content-type': 'multipart/form-data' },
  });
};

export const ImgUploadButton = forwardRef(({ children, ...props }, ref) => {
  let uploadRef = createRef();

  const { slug, onCreate, onUpdate, onError, alt_text = '', multiple, accept = 'image/*', ...rest } = props;

  function uploadFiles(evt) {
    // TODO: Ensure this filename has at most 100 characters
    const func = slug ? patchImage : postImage;
    const callback = slug ? onUpdate : onCreate;
    console.log('Uploading files', multiple);

    const images = [];
    const promises = [];

    Array.from(evt.target.files).forEach((file, i) => {
      const args = slug ? [slug, file, alt_text] : [file, alt_text];

      console.log('file', i);
      //   promises.push(func(...args).catch((err) => {}));
      promises.push(func(...args).catch((err) => {}));

      return;

      func(...args)
        .then((data) => {
          console.log('done', i);
          if (callback) {
            console.log('callback', i);
            if (multiple) {
              console.log('multiple', i);
              images.push(data);
              console.log(images);
            } else {
              console.log('not multile', i);
              callback(data);
            }
          }
        })
        .catch((err) => {
          if (onError) onError(err);
        });
    });

    Promise.all(promises).then((images) => {
      if (callback) {
        if (multiple) callback(images);
        else callback(images[0]);
      }
    });

    return;

    if (multiple && callback) {
      console.log('multiple callback');
      callback(images);
    }
  }

  return (
    <>
      <Button
        {...rest}
        id={props.id}
        className={getClassName(['miq-img-upload-btn', props.className])}
        onClick={() => uploadRef.click()}
        ref={ref}
      >
        {children || 'Upload Image'}
      </Button>
      <FileInput accept={accept} multiple={multiple} onChange={uploadFiles} ref={(file) => (uploadRef = file)} />
    </>
  );
});

ImgUploadButton.propTypes = propTypes;
