import { StaffService } from '@miq/adminjs';
import { addOrUpdateArrayObject } from '@miq/utils';
import { formatDate } from '@miq/utils';

import { UrlResolver } from '../utils';

export const docUrls = new UrlResolver('documents/');

export const formatDocDate = (date) => formatDate(date, { month: 'numeric', day: 'numeric', year: 'numeric' });

export const getDocumentLinkTo = (document, path) => {
  const link = `${path}${document.slug}/`;
  if (document.is_read_only) {
    return `${link}view/`;
  }
  return link;
};

export const mapDocumentUser2Avatar = (document) =>
  document.users_data.map(({ username, name, initials, slug, img_data }) => ({
    ...img_data,
    username,
    slug,
    name,
    initials,
  }));

class DocumentStaffService extends StaffService {
  constructor(path) {
    super(path);
  }

  archive(docSlug) {
    return this.get(`${this.path}${docSlug}/archive/`);
  }
  approve(docSlug) {
    return this.get(`${this.path}${docSlug}/approve/`);
  }

  // FILES //

  patchFile(docSlug, values) {
    if (!Object.keys(values).includes('action')) {
      return Promise.reject('Action required');
    }

    return this.post(values, null, `${this.path}${docSlug}/files/`);
  }

  // USERS //

  patchUser(docSlug, values) {
    if (!Object.keys(values).includes('action')) {
      return Promise.reject('Action required');
    }

    return this.post(values, null, `${this.path}${docSlug}/users/`);
  }

  patchUserPermission(docSlug, userSlug, permission) {
    return this.post({ user_slug: userSlug, permission }, null, `${this.path}${docSlug}/permission/`);
  }

  getStaffList(q) {
    return this.get('search-staff/', { q });
  }
}

export const docServices = new DocumentStaffService('documents/');

